/* Weather.css */

.weather-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 0px;
    width: 100%;
    height: 1116px;
    z-index: -1;
  }
  
  .weather-container {
    height: 100vh;
    font-family: fantasy;
    background-color: rgba(255, 255, 255, 0.8);
    font-size: large;
    text-align: center;
    width: 400px;
    height: 700px;
    border-radius: 30px;
    border: 2px solid black;
    box-shadow: 0 0 10px rgba(0, 0, 0, 2); 
    line-height: 50px;
  }
  
  
  .info {
    margin-left: 0px;
    text-align: center;
  }
  
  
  input {
    font-family: fantasy;
    text-align: center;
  }
  
